import React from 'react'
import PropTypes from 'prop-types'
import { Link, graphql } from 'gatsby'
import { kebabCase } from 'lodash'
import { HTMLContent } from '../components/Content'
import ArticleTemplate from '../components/ArticleTemplate'
import SE0 from '../components/SEO'
import Share from '../components/Share'
import Layout from '../components/Layout'
import Tag from '../components/Tag'
import '../assets/css/markdown1.css'

const defaultFormLink = 'https://blep-analytics.typeform.com/to/qmKYu8a6'

export default function JobListingPage ({ data }) {
	const { markdownRemark: post } = data

	const tags = post.frontmatter.tags
	return (
		<Layout includePreFooter>
			<div className='py-16 text-center text-purple-100 sm_py-32 bg-gradient-to-r from-orange-400 via-red-500 to-indigo-500'>
				<h1 className='text-3xl font-light sm_text-5xl'>
					{post.frontmatter.title}
				</h1>
				<h2 className='text-lg font-light sm_text-xl'>
					{post.frontmatter.location} – {post.frontmatter.commitment}
				</h2>
			</div>

			<section className='container flex-grow mx-auto mt-4 sm_mt-16'>
				<div className='mx-8'>
					<SE0
						title={post.frontmatter.title}
						meta_title={post.frontmatter.meta_title}
						meta_desc={post.frontmatter.meta_description}
						cover={post.frontmatter.cover}
						slug={post.fields.slug}
						date={post.frontmatter.date}
					/>

					<div>
						<div className='flex flex-wrap justify-between my-4'>
							<Link to='/careers' className='block hover_underline'>
								← Back to list
							</Link>

							{/* tags */}
							<div>
								<ul className='hidden taglist sm_block'>
									{(tags && tags.length)
										? tags.map(tag => (
											<li key={tag + `tag`} className='inline'>
												<Link to={`/tags/${kebabCase(tag)}/`}>
													<Tag key={tag} tag={tag} />
												</Link>
											</li>
										))
										: null}
								</ul>
							</div>
						</div>

						{/* <img className='my-4' src={post.frontmatter.cover} alt={post.frontmatter.title} /> */}

						<div className='my-8 markdown '>
							<HTMLContent content={post.html} />
						</div>

						<a href={post.frontmatter.link || defaultFormLink} target='noopener noreferer _blank'>
							<button className='w-full py-4 my-4 text-xl font-medium text-white uppercase bg-blue-400 hover_bg-blue-300 transition duration-200'>
								Apply Now
							</button>
						</a>

					</div>
				</div>

				{/* <Share
					title={post.frontmatter.title}
					slug={post.fields.slug}
					excerpt={post.frontmatter.meta_description}
        /> */}

			</section>
		</Layout>
	)
}

JobListingPage.propTypes = {
	data: PropTypes.shape({
		markdownRemark: PropTypes.object,
	}),
}

export const pageQuery = graphql`
  query JobListingByID($id: String!) {
    markdownRemark(id: { eq: $id }) {
      id
      html
      fields {
            slug
          }
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
        location
        commitment
        cover
        meta_title
        meta_description
        tags
        link
      }
    }
  }
`
